:root {
  --color-blue-link: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

.body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.header__nav {
  display: block;
}

.header__nav__ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav__li-1, .header__nav__li-2, .header__nav__li-3, .header__nav__li-4, .header__nav__li-5, .header__nav__li-6, .header__nav__li-7, .header__nav__li-8 {
  padding-right: 20px;
}

.header__nav__li-8 {
  padding-right: 0;
}

.header__nav__a {
  text-align: center;
  color: #000;
  align-items: center;
  height: 60px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header__nav__li-4 .header__nav__a:hover, .header__nav__a.is-active {
  color: var(--color-blue-link);
}

.is-active:after {
  content: "";
  background-color: var(--color-blue-link);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

